<template>
	<view class="search-wrapper">
		<search-box
			@handleInput="getKeywords"
			ref="searchbox"
		/>
		<match-list
			v-show="showRecommend"
			:matchList="matchList"
			:keywords="keywords"
			@search="search"
		/>
		<hot-list v-show="showHot" />
		<search-result
			v-show="showResult"
			:searchResult="searchResult"
		></search-result>
		<view class="loading" v-show="loading"></view>
	</view>
</template>

<script>
	import SearchBox from "../../components/Search/SearchBox";
	import MatchList from "../../components/Search/MatchList";
	import HotList from "../../components/Search/HotList";
	import SearchResult from "../../components/Search/SearchResult";
	import { mapState, mapMutations } from "vuex";
	export default {
		data() {
			return {
				// searchbox传过来的关键字
				keywords: "",
				// 匹配结果
				matchList: [],
				// 是否显示热搜
				showHot: false,
				showRecommend: false,
				// 是否显示搜索结果
				showResult: false,
				// 关键字和搜索结果
				searchResult: {
					keyword: "",
					resultList: []
				},
				// 页数
				page: 0
			}
		},
		methods: {
			getKeywords(keywords) {
				this.keywords = keywords;
				this.showHot = false;
				this.showRecommend = true;
				if (keywords.length !== 0) {
					this.$MyRequest({
						url: "/search/suggest",
						data: {
							keywords: keywords,
							type: "mobile"
						}
					}).then(
						(res, err) => {
							if (res.data.result.allMatch) {
								this.matchList = res.data.result.allMatch;
							} else {
								this.matchList = [];
							}
						}
					);
				} else{
					this.showHot = true;
				}
			},
			...mapMutations(["handleHistory","showLoading"]),
			search(key) {
				if(key !== this.keywords) {
					// 使关键字与搜索框里的一致
					this.$refs.searchbox.keywords = key
				}
				this.showLoading(true);
				this.handleHistory(key);
				this.showHot = false;
				this.showRecommend = false;
				this.$MyRequest({
					url: "/cloudsearch",
					data: {
						keywords: key,
						limit: 30,
						offset: this.page * 30
					}
				}).then(
					(res, err) => {
						let data = res.data.result.songs
						if(this.page == 0) {
							this.searchResult = {
								keyword: key,
								resultList: data
							}
							this.showResult = true;
						} else {
							this.searchResult.resultList = this.searchResult.resultList.concat(data)
						}
						this.showLoading(false);
					}
				);
			}
		},
		components:{
			SearchBox,
			MatchList,
			HotList,
			SearchResult
		},
		computed: {
			...mapState(["loading"])
		},
		watch: {
			keywords(newValue,oldValue) {
				if(newValue == "") {
					this.matchList = [];
					this.showHot = true;
					this.searchResult = {
						keyword: "",
						resultList: []
					};
					this.showResult = false;
					this.page = 0
				} else {
					this.showRecommend = true
				}
				if(newValue != oldValue) {
					this.searchResult = {
						keyword: "",
						resultList: []
					};
					this.showResult = false;
					this.page = 0
				}
			}
		},
		provide() {
			// 注入search方法
			let _this = this
			return {
				Search(key) {
					_this.search(key)
				}
			}
		},
		// 监听下拉触底事件
		onReachBottom() {
			if(this.showResult) {
				this.page++;
				this.search(this.keywords);
			}
		}
	}
</script>

<style lang="scss" scoped>
.search-wrapper{
		padding: 20rpx 40rpx;	
		.loading {
			padding-top: 20.1405rpx;
			width: 100%;
			height: 40.281rpx;
			background: url(data:image/gif;base64,) center no-repeat;
			background-size: 40.281rpx;
		}
}
</style>
